.hud #portfolio-types li::after {
  transition-duration: 0.15s;
  width: 0;
  display: block;
  background-color: #fff;
  height: 1px;
  content: '';
}
.hud #portfolio-types li:hover::after {
  width: 100%;
}
.hud .next,
.hud .return {
  position: absolute;
  background-color: rgba(0,0,0,0.85);
  border-radius: 50%;
  cursor: pointer;
  padding: 5px 10px 10px 10px;
  top: 10px;
  z-index: 2;
}
@media (min-width: 768px) {
  .hud .next,
  .hud .return {
    transform: translateY(-50%);
    top: 50vh;
  }
}
.hud .next img,
.hud .return img {
  width: 42px;
}
.hud .next {
  right: 5px;
}
@media (min-width: 768px) {
  .hud .next {
    right: 20px;
  }
}
.hud .return {
  left: 5px;
}
@media (min-width: 768px) {
  .hud .return {
    left: 20px;
  }
}
.hud #portfolio-types {
  z-index: 2;
  background-color: rgba(0,0,0,0.85);
  color: #fff;
  position: absolute;
  right: 0;
  border-radius: 42px 0 0 42px;
  padding: 10px;
  align-items: center;
  cursor: pointer;
  transition-duration: 0.25s;
  bottom: 0;
  transform: translateX(calc(100% - 66px));
}
.hud #portfolio-types img {
  width: 46px;
  height: 46px;
}
.hud #portfolio-types li {
  cursor: pointer;
  padding: 10px 20px;
  font-size: 22px;
}
@media (min-width: 576px) {
  .hud #portfolio-types li {
    padding: 0 20px;
    font-size: 18px;
  }
}
.hud #portfolio-types:hover {
  transform: translateX(0%);
}
